.reactions {
    display: flex;
    gap: 1rem;
    margin-top: 1.5rem;
}

/* Общий стиль кнопок */
.reaction-button {
    background-color: #f4f4f4;
    border: none;
    border-radius: 999px;
    padding: 0.5rem 1rem;
    font-size: 0.95rem;
    color: #444;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    transition: background-color 0.2s ease, transform 0.2s ease, box-shadow 0.2s ease;
}

/* Hover эффект */
.reaction-button:hover {
    background-color: #e2e2e2;
    transform: scale(1.05);
}

/* Активные состояния */
.reaction-button.liked {
    box-shadow: 0 0 0 2px #34c759; /* зелёное свечение */
    background-color: #eaffea;
    color: #228822;
}

.reaction-button.disliked {
    box-shadow: 0 0 0 2px #ff3b30; /* красное свечение */
    background-color: #ffecec;
    color: #bb2222;
}
