.post-page {
    max-width: 800px;
    margin: 2rem auto;
    background-color: #ffffff;
    border-radius: 16px;
    padding: 2rem;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.05);
    color: #222;
}

.post-page img {
    width: 100%;
    height: auto;
    border-radius: 12px;
    margin-bottom: 2rem;
    object-fit: cover;
}

.post-page h1 {
    font-size: 2.5rem;
    font-weight: 800;
    margin-bottom: 1rem;
    line-height: 1.3;
}

.post-page p {
    font-size: 1.125rem;
    line-height: 1.8;
    color: #444;
    margin-bottom: 2rem;
}
