.post-card {
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.04);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    cursor: pointer;
}

.post-card:hover {
    transform: translateY(-6px);
    box-shadow: 0 10px 24px rgba(0, 0, 0, 0.08);
}

.post-card.full {
    min-width: 100%;
    padding: 2rem;
    background: #f7f9fb;
    display: grid;
}

.post-title {
    font-size: 1.25rem;
    font-weight: 700;
    margin-bottom: 0.75rem;
    line-height: 1.4;
}

.post-card.full .post-title {
    font-size: 2rem;
}

.post-body {
    color: #555;
    line-height: 1.6;
    font-size: 1rem;

    /* Ограничение текста */
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}

.img_post {
    max-width: 26.9rem;

    display: grid;
    .img_post_ns {
        max-width: 27rem;
        max-height: 43rem;
    }
}

.read-more-button {
    padding: 6px 18px;
    border-radius: 999px;
    border: 1.5px solid black;
    background: transparent;
    font-size: 0.95rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.read-more-button:hover {
    background-color: black;
    color: white;
}